.inevitability-block {
    position: relative;
    padding-bottom: 140px;
    padding-left: 56px;
    padding-right: 56px;
    background: url("../img/inevitability-bg-1@1x.png") top left no-repeat,
        url("../img/inevitability-bg-2@1x.png") top right no-repeat,
        linear-gradient(to bottom, #ffb700 37%, rgba(228, 227, 233, 1) 37%);
    background-size: 34.9% auto, 54.56% auto, auto;
    z-index: 0;
    overflow: hidden;

    @media (min-width: 1441px) {
        background: url("../img/inevitability-bg-1@1x.png") top left no-repeat,
            url("../img/inevitability-bg-2@1x.png") top right no-repeat,
            #ffb700;
        background-size: 34.9% auto, 54.56% auto, auto;
    }

    @media (min-width: 1441px) and (min-resolution: 1.5dppx),
        (min-width: 1441px) and (min-resolution: 144dpi) {
        background: url("../img/inevitability-bg-1@2x.png") top left no-repeat,
            url("../img/inevitability-bg-2@2x.png") top right no-repeat,
            #ffb700;
        background-size: 34.9% auto, 54.56% auto, auto;
    }

    @media (max-width: 1200px) {
        background: url("../img/inevitability-bg-1@1x.png") top left no-repeat,
            url("../img/inevitability-bg-2@1x.png") top right no-repeat,
            url("../img/inevitability-bottom@1x.png") bottom 1% left 0% no-repeat,
            linear-gradient(to bottom, #ffb700 36%, rgba(228, 227, 233, 1) 36%);
        background-size: 34.9% auto, 54.56% auto, 100% auto, auto;
    }

    @media (max-width: 1200px) and (min-resolution: 1.5dppx),
        (max-width: 1200px) and (min-resolution: 144dpi) {
        background: url("../img/inevitability-bg-1@2x.png") top left no-repeat,
            url("../img/inevitability-bg-2@2x.png") top right no-repeat,
            url("../img/inevitability-bottom@2x.png") bottom 1% left 0% no-repeat,
            linear-gradient(to bottom, #ffb700 36%, rgba(228, 227, 233, 1) 36%);
        background-size: 34.9% auto, 54.56% auto, 100% auto, auto;
    }

    @media (max-width: 900px) {
        padding-left: 2%;
        padding-right: 2%;
        background: url("../img/inevitability-bg-1@1x.png") top left no-repeat,
            url("../img/inevitability-bg-2@1x.png") top right no-repeat,
            linear-gradient(to bottom, #ffb700 34%, rgba(228, 227, 233, 1) 34%);
        background-size: 34.9% auto, 54.56% auto, auto;
    }

    @media (max-width: 900px) and (min-resolution: 1.5dppx),
        (max-width: 900px) and (min-resolution: 144dpi) {
        background: url("../img/inevitability-bg-1@2x.png") top left no-repeat,
            url("../img/inevitability-bg-2@2x.png") top right no-repeat,
            linear-gradient(to bottom, #ffb700 34%, rgba(228, 227, 233, 1) 34%);
        background-size: 34.9% auto, 54.56% auto, auto;
    }

    @media (max-width: 768px) {
        padding-left: 3%;
        padding-right: 3%;
        background: url("../img/inevitability-top-bg@1x.png") top no-repeat,
            linear-gradient(to bottom, #ffb700 30%, rgba(228, 227, 233, 1) 30%);
        background-size: 100% auto, auto;
    }

    @media (max-width: 768px) and (min-resolution: 1.5dppx),
        (max-width: 768px) and (min-resolution: 144dpi) {
        background: url("../img/inevitability-top-bg@2x.png") top no-repeat,
            linear-gradient(to bottom, #ffb700 30%, rgba(228, 227, 233, 1) 30%);
        background-size: 100% auto, auto;
    }

    @media (max-width: 540px) {
        padding-bottom: 70px;
        padding-left: 20px;
        padding-right: 20px;
        background: url("../img/inevitability-top-bg@1x.png") top no-repeat,
            linear-gradient(to bottom, #ffb700 30%, rgba(228, 227, 233, 1) 30%);
        background-size: 100% auto, 95% auto, auto;
    }

    @media (max-width: 540px) and (min-resolution: 1.5dppx),
        (max-width: 540px) and (min-resolution: 144dpi) {
        background: url("../img/inevitability-top-bg@2x.png") top no-repeat,
            linear-gradient(to bottom, #ffb700 30%, rgba(228, 227, 233, 1) 30%);
        background-size: 100% auto, auto;
    }

    &__title {
        margin-bottom: 200px;
        padding-top: 195px;

        @media (max-width: 900px) {
            margin-bottom: 140px;
        }

        @media (max-width: 768px) {
            margin-bottom: 250px;
        }

        @media (max-width: 540px) {
            margin-bottom: 75px;
            padding-top: 130px;
        }
    }

    &__subtitle {
        margin-bottom: 35px;
        line-height: 37px;
        font-size: 28px;

        @media (max-width: 1200px) {
            line-height: 30px;
            font-size: 26px;
        }

        @media (max-width: 900px) {
            margin-bottom: 15px;
        }

        @media (max-width: 768px) {
            line-height: 24px;
            font-size: 22px;
        }

        @media (max-width: 540px) {
            line-height: 21px;
            font-size: 16px;
        }
    }

    &__founder-image {
        position: absolute;
        top: 80px;
        right: 100px;
        width: 397px;
        height: auto;

        @media (max-width: 1200px) {
            width: 27.57%;
        }

        @media (max-width: 768px) {
            top: 700px;
            right: 0;
            width: 230px;
        }

        @media (max-width: 540px) {
            top: 470px;
            right: -10px;
            width: 160px;
            overflow-x: hidden;
        }
    }

    &__scientist {
        position: absolute;
        top: 850px;
        left: 4%;
        width: 436px;
        height: 450px;
        background-image: url("../img/inevitability-scientist@1x.png");
        background-repeat: no-repeat;
        background-size: contain;

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url("../img/inevitability-scientist@2x.png");
        }

        @media (max-width: 1200px) {
            top: 720px;
            left: 4%;
            width: 25%;
            height: 28%;
        }

        @media (max-width: 900px) {
            top: 670px;
        }

        @media (max-width: 768px) {
            top: 1450px;
            width: 180px;
            background-image: url("../img/inevitability-scientist-mobile@1x.png");
        }

        @media (max-width: 768px) and (min-resolution: 1.5dppx),
            (max-width: 768px) and (min-resolution: 144dpi) {
            background-image: url("../img/inevitability-scientist-mobile@2x.png");
        }

        @media (max-width: 540px) {
            top: 1000px;
            height: 220px;
        }
    }

    &__ellips {
        position: absolute;
        top: 1300px;
        right: 0;
        width: 124px;
        height: 250px;
        background-image: url("../img/inevitability-ellips@1x.png");
        background-repeat: no-repeat;
        background-size: contain;

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url("../img/inevitability-ellips@2x.png");
        }

        @media (max-width: 1200px) {
            top: 1150px;
            width: 100px;
            height: 200px;
        }

        @media (max-width: 768px) {
            display: none;
        }
    }

    &__cloud {
        display: block;
        position: absolute;

        &_population {
            z-index: -1;
            top: 13.5%;
            left: 0;
            width: 1035px;
            height: auto;

            @media (max-width: 1200px) {
                width: 900px;
            }

            @media (max-width: 900px) {
                top: 15.5%;
                width: 700px;
            }

            @media (max-width: 768px) {
                top: 10%;
                left: -50px;
                width: 750px;
                height: auto;
            }

            @media (max-width: 540px) {
                top: 160px;
                width: 450px;
            }
        }

        &_problems {
            z-index: -2;
            top: 33%;
            right: 0;
            width: 871px;
            height: auto;

            @media (max-width: 1200px) {
                width: 771px;
            }

            @media (max-width: 900px) {
                top: 30%;
                width: 550px;
            }

            @media (max-width: 768px) {
                top: 20%;
            }

            @media (max-width: 540px) {
                top: 370px;
                width: 340px;
            }
        }

        &_club {
            z-index: -1;
            top: 47%;
            left: 0;

            width: 500px;
            height: auto;
            @media (min-width: 769px) {
                display: none;
            }

            @media (max-width: 540px) {
                top: 750px;
                width: 330px;
            }
        }

        &_technologies {
            z-index: -1;
            top: 61%;
            left: 0;
            width: 824px;
            height: auto;

            @media (max-width: 1200px) {
                top: 53%;
                width: 724px;
            }

            @media (max-width: 900px) {
                top: 53.8%;
                width: 600px;
            }

            @media (max-width: 768px) {
                top: 61%;
                right: 0;
                left: auto;
                width: 500px;
            }

            @media (max-width: 540px) {
                top: 1070px;
                width: 330px;
            }
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    &__item {
        &_population {
            margin-bottom: 180px;
            margin-right: auto;
            max-width: 847px;
            line-height: 30px;
            font-size: 22px;

            @media (max-width: 1200px) {
                max-width: 747px;
                line-height: 22px;
                font-size: 20px;
            }

            @media (max-width: 900px) {
                margin-bottom: 70px;
                max-width: 600px;
            }

            @media (max-width: 768px) {
                line-height: 20px;
                font-size: 18px;

                margin-bottom: 270px;
                width: 72%;
            }

            @media (max-width: 540px) {
                margin-bottom: 170px;
                width: 250px;
                line-height: 16px;
                font-size: 14px;
            }
        }

        &_problems {
            margin-left: auto;
            max-width: 690px;
            line-height: 40px;
            font-size: 26px;
            color: #ffffff;
            text-align: right;

            @media (max-width: 1200px) {
                line-height: 24px;
                font-size: 22px;
            }

            @media (max-width: 900px) {
                max-width: 500px;
                line-height: 22px;
                font-size: 20px;
            }

            @media (max-width: 768px) {
                margin-bottom: 200px;
                line-height: 20px;
                font-size: 18px;
            }

            @media (max-width: 540px) {
                margin-bottom: 130px;
                width: 300px;
                line-height: 16px;
                font-size: 14px;
            }
        }

        &_club {
            margin-bottom: 200px;
            margin-left: auto;
            max-width: 690px;
            line-height: 40px;
            font-size: 26px;
            color: #ffffff;
            text-align: right;

            @media (max-width: 1200px) {
                line-height: 24px;
                font-size: 22px;
            }

            @media (max-width: 900px) {
                max-width: 500px;
                line-height: 22px;
                font-size: 20px;
            }

            @media (max-width: 768px) {
                margin-left: 0;
                margin-right: auto;
                max-width: 300px;
                line-height: 20px;
                font-size: 18px;
                color: inherit;
                font-weight: 600;
                text-align: left;
            }

            @media (max-width: 540px) {
                margin-bottom: 220px;
                line-height: 16px;
                font-size: 14px;
            }
        }

        &_technologies {
            margin-right: auto;
            max-width: 663px;
            line-height: 40px;
            font-size: 24px;

            @media (max-width: 1200px) {
                max-width: 563px;
                line-height: 28px;
                font-size: 26px;
            }

            @media (max-width: 900px) {
                max-width: 480px;
                line-height: 26px;
                font-size: 24px;
            }

            @media (max-width: 768px) {
                margin-right: 0;
                margin-left: auto;
                max-width: 400px;
                line-height: 20px;
                font-size: 18px;
                text-align: right;
            }

            @media (max-width: 540px) {
                margin-bottom: 110px;
                width: 280px;
                line-height: 16px;
                font-size: 14px;
            }
        }

        &_join-us {
            margin-left: auto;
            max-width: 726px;
            line-height: 42px;
            font-size: 30px;
            font-weight: 600;
            text-align: right;

            @media (max-width: 1200px) {
                margin-left: auto;
                max-width: 526px;
                line-height: 30px;
                font-size: 26px;
                font-weight: 600;
                text-align: right;
                margin-top: 150px;
            }

            @media (max-width: 900px) {
                margin-top: 100px;
                line-height: 26px;
                font-size: 24px;
            }

            @media (max-width: 768px) {
                max-width: 100%;
                margin-top: 300px;
                margin-right: auto;
                line-height: 20px;
                font-size: 18px;
                text-align: inherit;
            }

            @media (max-width: 540px) {
                margin-top: 0;
                line-height: 16px;
                font-size: 14px;
            }
        }
    }

    &__founder-name {
        margin: 0;
        margin-top: 35px;
        margin-bottom: 8px;

        @media (max-width: 768px) {
            margin-top: 55px;
            text-align: right;
        }

        @media (max-width: 540px) {
            margin-top: 30px;
        }
    }

    &__founder {
        margin: 0;

        @media (max-width: 768px) {
            text-align: right;
        }
    }

    &__bottom-bg {
        position: absolute;
        z-index: -1;
        right: 0;
        bottom: -100px;
        left: 0;
        width: 350px;
        height: auto;
        margin-left: auto;
        margin-right: auto;

        @media (min-width: 769px) {
            display: none;
        }
    }

    &__bottom {
        position: absolute;
        bottom: 1%;
        left: 0;
        width: 100vw;
        height: auto;
    }
}
